<template>
    <div class="input-component">
        <div class="input-wrapper" v-show="true">
            <textarea
                class="chat-input"
                type="text"
                v-model="text"
                @keydown.enter.prevent.exact="send_text"
                @keydown.enter.ctrl.prevent="make_new_line"
                rows="1"
            ></textarea>
            <transition name="fade" mode="out-in">
                <picker
                    v-show="display_emoji"
                    @select="insert_emoji" 
                    :style="{ width: '325px', position: 'absolute', bottom: '32px', right: '-60px', fontSize: '0.9em', fontFamily: 'Segoe UI' }" 
                    :exclude="exclude"
                    set="emojione"
                    :showPreview="false"
                    :showSearch="false"
                />
            </transition>
        
        </div>
        <div class="iconbutton" @click="toggle_emoji_picker">
            <div class="leftdummy">
                <div class="idummy">
                    <i class="far fa-smile"></i>
                </div>
            </div>
        </div>
        <div class="iconbutton" v-if="send" @click="send_text($event)">
            <div class="rightdummy">
                <div class="idummy">
                    <i class="far fa-paper-plane"></i>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import f from '@/application/functions';
import { Picker } from 'emoji-mart-vue'
import autosize from 'autosize'

export default {
    name: 'InputField',
    components: {
        Picker
    },

    watch: {
        text(current) {
            if(current) {
                this.send = true
                this.$emit('FilledInput')
            } else {
                this.send = false
                this.$emit('emptyInput')
            }  
        }
    },

    computed: {
        picker_height() {
            return window.innerHeight * 0.9
        }
    },

    methods: {
        toggle_emoji_picker() {
            this.display_emoji = !this.display_emoji
        },
        make_new_line() {
            if(!this.text) return

            this.text += '\n'
            let add_height = $('.chat-input').height() + 24
            $('.chat-input').css('height', add_height)
        },
        send_text(event) {

            event.textContent = this.text

            if(this.text && this.text !== '\n') {
                this.display_emoji = false
                this.$emit('chatMessage', event)
                this.$emit('emptyInput')
                this.send = false
                this.text = ''
                $('.chat-input').css('height', '24px')
            }
            // if(this.text == '\n') {
            //     this.text = ''
            //     $('.chat-input').css('height', '24px')
            // }
        },

        insert_emoji(emoji) {
            this.text = this.text + emoji.native;
        }
    },  
    data() {
        return {
            send: false,
            toMatch: [
                /Android/i,
                /webOS/i,
                /iPhone/i,
                /iPad/i,
                /iPod/i,
                /BlackBerry/i,
                /Windows Phone/i
            ],
            isPhone: true,
            heightUp: 40,
            areaValue: '',
            ready : false,
            text: '',
            exclude: ['flags'],
            display_emoji: false,
            text_area_height: ''
        }
    },

    mounted() {
        this.isPhone = this.toMatch.some(toMatchItem => {
            return navigator.userAgent.match(toMatchItem);
        })

        autosize($('.chat-input'))
    }
}
</script>

<style lang="sass" scoped>

.input-component
    width: 100%
    display: flex
    justify-content: center
    align-items: center
    padding: 0 2 * $r
    padding-right: 0
    .input-wrapper
        display: flex
        justify-content: center
        align-items: center
        position: relative
        width: 100%
    .right
        margin: 0 $r
    .iconbutton

        .leftdummy
            width: 20px

        .rightdummy
            width: 35px

        .idummy
            width: 100%
            height: 35px
            line-height: 35px
            i
                line-height: 35px!important
                
.chat-input
    width: 100%
    max-height: 63px
    overflow-y: auto
    resize: none
    &::-webkit-scrollbar
        width: 0!important

.fade-enter-active, .fade-leave-active
    transition: all .2s ease-in

.fade-enter, .fade-leave-to
    opacity: 0
</style>